import React from 'react'
import { useDispatch } from 'react-redux'
import { actions4 } from '../../store/Index'
import "./BalloonsItem.css"
import { useEffect } from 'react'
import { useState } from 'react'
function BalloonsItem(props) {
  const [className,setClassName] = useState('btn btn-primary')
  const dispatch = useDispatch();
  const clickIsFav=()=>{
     toggleFav()
      dispatch(actions4.toggleFavorite(props.id))

    }
  
  useEffect(() => toggleFav())

  function toggleFav () {
    if(props.isFav){
      setClassName("btn btn-danger")
    }
  }
 
    return (
                 
            <div className="li2">
        <h1>{props.name} </h1>
     
        <h2>{props.price} :××—×™×¨</h2>

      <img src={props.poster} alt="img2" width="180px" height="180px" ></img> 

        <h6>{props.isFav}</h6>

        <div className="button3">
        <button type="button" onClick={ () => clickIsFav()}  className={className}>×”×•×¡×£ ×œ×”×–×× ×”</button>
        </div>
        
        </div>
       
    )
   
}

export default BalloonsItem
