import React from 'react'
import { useDispatch } from 'react-redux';
import { actions2 } from '../../../store/Index';
import './FlowerInBoxeseItem.css'


function FlowerInBoxeseItem(props) {
  const dispatch = useDispatch();

  const clickIsFav=()=>{
    dispatch(actions2.toggleFavorite(props.id))
  }
  
    let className = 'btn btn-primary'
    if(props.isFav){
      className = "btn btn-danger"
    }
    return (
        <div>
      
        <div className="li1">
         <h1>  {props.name}</h1>
      
         <h2>{props.price} :××—×™×¨</h2>

         <img src={props.poster} alt="img1" width="180px" height="180px" ></img> 
 
         <h6>{props.isFav}</h6>

        <div className="button1">
         <button onClick={clickIsFav} className={className}>×”×•×¡×£ ×œ×”×–×× ×”</button>
         </div>
        
         </div>
        
         </div>
    )
}

export default FlowerInBoxeseItem
