import React from 'react'
import { useSelector } from 'react-redux'
import OrdersItem from './OrdersItem'
import './OrdersList.css'

function Orders(props) {
    const flowers = useSelector(state=>state.storeFlowers.flowers).filter(f=>f.isFav)
    const head = useSelector(state=>state.storeBirthdayHeads.BirthdayHeads).filter(f=>f.isFav)
    const FlowerInBoxese = useSelector(state => state. storeFlowersInBoxese.FlowersInBoxese).filter(f=>f.isFav)
    const Balloons = useSelector(state=>state.storeBalloons.Balloons).filter(f=>f.isFav)
    return (
       <div className="allOrder">
        <ul >
       {flowers.map((f)=><OrdersItem
       id ={f.id}
       key = {f.id}
       name = {f.name}
       price = {f.price}
       isFav = {f.isFav}
       poster = {f.poster}
      
       />)}
   </ul>

   


<ul >
{head.map((f)=><OrdersItem
id ={f.id}
key = {f.id}
name = {f.name}
price = {f.price}
isFav = {f.isFav}
poster = {f.poster}

/>)}
</ul>



<ul >
{FlowerInBoxese.map((f)=><OrdersItem
id ={f.id}
key = {f.id}
name = {f.name}
price = {f.price}
isFav = {f.isFav}
poster = {f.poster}

/>)}
</ul>



<ul >
{Balloons.map((f)=><OrdersItem
id ={f.id}
key = {f.id}
name = {f.name}
price = {f.price}
isFav = {f.isFav}
poster = {f.poster}

/>)}
</ul>
</div>
    )
}

export default Orders
