import React from 'react'
import { useDispatch } from 'react-redux'
import { actions3 } from '../../store/Index';
import './BirthdayHeadsItem.css'
import { useEffect } from 'react';
import { useState } from 'react';
function BirthdayHeadsItem(props) {
    const dispatch = useDispatch();
    const [className,setClassName] = useState('btn btn-primary')
    const clickIsFav=()=>{
        dispatch(actions3.toggleFavorite(props.id))
        toggleFav()
      }
      useEffect(() => toggleFav())

      function toggleFav () {
        if(props.isFav){
          setClassName("btn btn-danger")
        }
      }
     
    return (
        <div>
                 
       <div className="li3">
        <h1> ×§×©×ª {props.name}  </h1>
     
        <h2>{props.price} :××—×™×¨</h2>

        <h6>{props.isFav}</h6>

        <img src={props.poster} alt="img1" width="180" height="180px" ></img> 

        <div className="button2">
        <button type="button" onClick={() => clickIsFav()}  className={className}>×”×•×¡×£ ×œ×”×–×× ×”</button>
        </div>
       
       
        </div>    
      
        </div>
    )
}

export default BirthdayHeadsItem
