import { configureStore, createSlice } from "@reduxjs/toolkit";
import flowers from "./Flowers";
import FlowersInBoxese from './FlowersInBoxese';
import BirthdayHeads from './BirthdayHeads'
import Balloons from './Balloons'





const flowersSlice = createSlice({

  initialState:{
    flowers: flowers,
    // xx:yy
    show:true
  },
  name:"flowers",
  reducers:{
    toggleFavorite:(state,action)=>{
         let index= state.flowers.findIndex(f=>f.id === action.payload)
         state.flowers[index].isFav = !state.flowers[index].isFav
          
        },
      

  }
})



const FlowersInBoxeseSlice = createSlice({

  initialState:{
    FlowersInBoxese: FlowersInBoxese,
    // xx:yy
    show:true
  },
  name:"FlowersInBoxese",
  reducers:{
    toggleFavorite:(state,actions)=>{
         let index= state.FlowersInBoxese.findIndex(f=>f.id === actions.payload)
         state.FlowersInBoxese[index].isFav = !state.FlowersInBoxese[index].isFav
          
        },
      

  }
})


const BirthdayHeadsSlice = createSlice({

  initialState:{
    BirthdayHeads: BirthdayHeads,
    // xx:yy
    show:true
  },
  name:"BirthdayHeads",
  reducers:{
    toggleFavorite:(state,actions)=>{
         let index= state.BirthdayHeads.findIndex(f=>f.id === actions.payload)
         state.BirthdayHeads[index].isFav = !state.BirthdayHeads[index].isFav
          
        },
      

  }
})


const BalloonsSlice = createSlice({

  initialState:{
    Balloons: Balloons,
    // xx:yy
    show:true
  },
  name:"Balloons",
  reducers:{
    toggleFavorite:(state,actions)=>{
      let index = state.Balloons.findIndex(f=>f.id === actions.payload)
      state.Balloons[index].isFav = !state.Balloons[index].isFav
          
        },
      

  }
})





const store = configureStore({
  reducer:{
    storeFlowers:flowersSlice.reducer, 
    storeFlowersInBoxese:FlowersInBoxeseSlice.reducer,
    storeBirthdayHeads:BirthdayHeadsSlice.reducer,
    storeBalloons:BalloonsSlice.reducer




  }
})


export const actions = flowersSlice.actions
export const actions2 = FlowersInBoxeseSlice.actions
export const actions3 = BirthdayHeadsSlice.actions
export const actions4= BalloonsSlice.actions




export default store